.CustomCheckBox{
    display: block;
    margin-top: 20px;
    cursor: pointer; 
    float: left;
}

.CustomCheckBox::before{
    content: "";
    display: inline-block;
    height: 25px;
    width: 25px;
    margin-right: 10px;
    border-radius: 5px;
    background: #222222;
    vertical-align: middle;
}
input[type=checkbox]:checked ~ .CustomCheckBox::before
{
    background: red;
}

