.SlideShow{
    width: 100%;
    height: 100%;
    position: relative;
}
.SlideShow  .Button{
    border:none;
    width:50px;
    height: 50px;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.SlideShow  .ArrowLeft{
    left: 0;
    background: url(/static/media/ArrowLeft.7d765d97.png);
    background-size: 50px 50px;
    z-index: 1;
}
.SlideShow  .ArrowRight{
    right: 0;
    background: url(/static/media/ArrowRight.5018c780.png);
    background-size: 50px 50px;
    z-index: 1;
}
.SlideShow .DotsOuter{
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 40px;
    z-index: 1;
}
.SlideShow .DotsOuter .DotsContainer{
    display: inline-block;
    height:100%;
}
.SlideShow .DotsOuter .DotsContainer img{
    margin: 0 5px;
}
.SlideShow  .SlideContainer{
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.SlideShow .SlideContainer .Slide img{
    display: block;
}
.SlideShow .SlideContainer .Slide{
    position: absolute;
    display: inline-flex;
    height: 100%;
    min-width: 100%;
    left: 0;
    -webkit-transition: 1s;
    transition: 1s;
}
